/*type color variables */
/*border color variables */
/*type size variables */
.header {
  font-size: 16px;
  padding: 0px 30px 0px 30px;
  line-height: 40px;
  position: fixed;
  top: 0px;
  width: 100%;
  z-index: 2;
  display: inline-block;
  background-color: transparent;
  -webkit-transition: background 500ms ease;
  transition: background 500ms ease;
}
.header:hover {
    background-color: rgba(255, 255, 255, 0.5);
}
.header a {
    color: #222222;
}
.header .nav-holder {
    margin: 0 0 0 20px;
}
.header #site-title {
    z-index: 2;
    display: inline-block;
    font-family: "NeueHaasRegularTX", Helvetica, Arial, sans-serif;
}
.header #site-title h1 {
      display: inline-block;
}
.header .side-menu {
    z-index: 3;
    padding: 20px;
    line-height: 38px;
    font-size: 32px;
    position: fixed;
    top: 0px;
    height: 100vh;
    left: 100%;
    background-color: white;
    width: 50vw;
    letter-spacing: 0.25px;
    font-weight: normal;
    -webkit-transition: left 500ms ease;
    transition: left 500ms ease;
}
.header .side-menu.visible {
      left: 50%;
}
.header .side-menu .linkGroup {
      margin-bottom: 10px;
}
.header .side-menu .list {
      max-height: calc(100vh - 140px);
      overflow: auto;
      font-size: 24px;
      line-height: 28px;
      padding-top: 20px;
}
.header .side-menu .list .popup-city-li {
        margin-bottom: 28px;
}
.header .side-menu .list .popup-city-li .table {
          display: table;
          min-height: 56px;
          vertical-align: middle;
          float: left;
}
.header .side-menu .list .popup-city-li .table .table-cell {
            vertical-align: middle;
            display: table-cell;
}
.header .side-menu .list .popup-city-li .table .table-cell img {
              box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
              width: 50px;
}
.header .side-menu .list .popup-city-city,
      .header .side-menu .list .popup-city-region {
        padding-left: 65px;
        display: block;
}
.header .side-menu .list .popup-city-region {
        color: #929292;
}
.header .side-menu input {
      color: #222222;
      height: 38px;
      margin-top: 62px;
      border: none;
      width: 100%;
      line-height: 38px;
      font-size: 32px;
      border-bottom: 1px solid #e7e7e7;
}
.header .side-menu input:focus {
        outline: none;
}
.header .side-menu input::-webkit-input-placeholder {
        color: #222222;
}
.header .side-menu input:-ms-input-placeholder {
        color: #222222;
}
.header .side-menu input::placeholder {
        color: #222222;
}
.header #search-icon,
  .header #menu-icon {
    z-index: 4;
    width: 23px;
    height: 23px;
    position: fixed;
    top: 11px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    display: inline-block;
    cursor: pointer;
}
.header #search-icon {
    background-image: url("/img/search.svg");
    right: 74px;
}
.header #menu-icon {
    background-image: url("/img/hamburger.svg");
    right: 24px;
    width: 29px;
}
.header #menu-icon.open {
    background-image: url("/img/close.svg");
    width: 23px;
    right: 27px;
}
@media only screen and (min-width: 0) and (max-width: 950px) {
.header .side-menu {
    width: 100vw;
}
.header .side-menu.visible {
      left: 0%;
}
.header #menu-icon {
    width: 23px;
    top: 9px;
}
.header #menu-icon.open {
      top: 11px;
      width: 19px;
}
.header #search-icon {
    width: 19px;
    top: 9px;
    right: 60px;
}
}



